@import '../../assets/mixins/mixins.scss';

.CTA {
    margin: 0 auto;
    width: fit-content;
    height: 29px;
    padding: 0 10px;
    border-radius: 25px;
    display: flex;
    background-color: white;
    user-select: none;
    cursor: pointer;

    &__Text {
        margin: auto;
        font-weight: bold;
        font-family: Helvetica, sans-serif;
        font-size: 10px;
        color: #51B03E;
        
    }

}

@include tablet-p {

    .CTA {
        margin: 0 0 0 40px;
        width: fit-content;
        height: 45px;
        padding: 0 10px;
        border-radius: 25px;
        display: flex;
        background-color: white;
        user-select: none;
        cursor: pointer;
    
        &__Text {
            margin: auto;
            font-weight: bold;
            font-family: Helvetica, sans-serif;
            font-size: 12px;
            color: #51B03E;
            
        }
    
    }
}