@import '../../assets/mixins/mixins.scss';

.VLine {
    max-height: 100%;
    width: 3px;
    background-color: #51B03E;
    border-radius: 10px;
    margin: 0 6px 0 0;
}

@include tablet-p {

    .VLine {
        max-height: 100%;
        width: 8px;
        background-color: #51B03E;
        border-radius: 10px;
        margin: 0 10px;
    }
}