@import '../../assets/mixins/mixins.scss';

.Cards {
    height: calc(100vh - 305px);
    background-color: rgba(177, 177, 177, 0.45);
    position: relative;

    &__Container {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        position: absolute;
        top: -30px;
    }
}

@include tablet-p {
    .Cards {
        height: 242px;
        background-color: rgba(177, 177, 177, 0.45);
        position: relative;
    
        &__Container {
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-evenly;
            position: absolute;
            top: -59px;
        }
    }
}