.Questionnaire {
    position: absolute;
    top: 84px;
    right: 0;
    width: 443px;
    height: 290px;
    box-shadow: -20px 15px rgba(0, 0, 0, 0.16);
    background-image: url('../../assets/imgs/solar.jpeg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    display: flex;

    // &__Blur {
    //     width: 265px;
    //     height: 116px;
    //     background-color: #6367707a;
    //     position: absolute;
    // }

    &__Container {
        margin: auto;
        color: #FFF;
        font-size: 29px;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: bold;
        text-align: center;
        

        &__Title {
            margin-bottom: 47px;
        }

        &__Options {

            &__Container {
                &__Yes {
                    cursor: pointer;
                }

                &__No {
                    cursor: pointer;
                }
            }
        }
    }
}