.QuestionnaireViewer {
    position: fixed;
    width: 100vw;
    height: 100vh;
    display: flex;
    background-color: #80808085;
    z-index: 500;

    &__Container {
        margin: auto;
        background-image: url('../../../assets/imgs/solar.jpeg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        width: 60%;
        height: 60%;

        &__X {
            position: fixed;
            top: 60px;
            right: 60px;
            font-family: Arial, Helvetica, sans-serif;
            font-size: 36px;
            font-weight: bold;
            cursor: pointer;
        }
    }
}