@import '../../assets/mixins/mixins.scss';

.Hero {
    height: 306px;
    background-color: #242B3A;
    display: flex;

    &__Left-Container {
        width: 100%;
        padding-top: 80px;
        display: flex;
        flex-direction: column;
    }

    &__Right-Container {
        display: none;
    }
}

@include tablet-p {
    
    .Hero {
        height: 575px;
        background-color: #242B3A;
        display: flex;
    
        &__Left-Container {
            width: 50%;
            padding-left: 86px;
            padding-top: 160px;
        }
    
        &__Right-Container {
            display: inline-block;
            width: 50%;
            position: relative;
        }
    }
}